<template>
    <main class="veins">
        <div class="container">
            <h1>Villagers</h1>
            <label for="selector-especies" class="label">Choose an specie</label>
            <b-form-select id='selector-especies' v-model="selected" :options="especies"></b-form-select>
            <div class="row m-4 row-veins"
            >
                <div class="col-12" v-for="especie in especies.filter(e => e == selected)" :key="especie">
                    <div class="row">
                        <div class="col-lg-6 col-12 p-4 card-container" 
                            v-for="vei in veins.filter(v => v.species == especie)" 
                            :key="vei.id"
                            >
                            <CardVeins :vei='vei'/>
                        </div>
                    </div>
                </div>
                
            </div>
            
        </div>
    </main>
</template>

<script>
import CardVeins from '@/components/CardVeins.vue';
import axios from "axios";

export default {
    name: 'Veins',
    components: {
        CardVeins
    },
    mounted () {
        axios
            .get('https://acnhapi.com/v1/villagers')
            .then((response) => {
                for(var i in response.data)
                this.veins.push(response.data[i]);
            });
    },
    data: function(){
        return{
            veins: [],
            selected: 'Anteater'
        }
    },
    computed: {
        especies: function(){
            var e = [];
            for(var i in this.veins){
                if (!e.includes(this.veins[i].species)){
                    e.push(this.veins[i].species);
                }
            }
            return e;
        }
    }
}
</script>

<style>
    .label{
        font-weight: 500;
        color: #f8f8f0;
        font-size: 24px;
    }
    #selector-especies{
        font-size: 20px;
        color: rgb(107, 92, 67);
    }
    @media only screen and (max-width: 425px) {
        .row-veins{
            margin: 0px !important;
        }
    }
</style>
