<template>
  <div id="app">
    <NavBar :isNotHome='isNotHome'/>
    <router-view/>
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'

export default {
  components: {
    NavBar
  },
  data: function () {
    return {
        isNotHome: false
    }
  },
  mounted (){
    var pathname = window.location.pathname;
    if(pathname == '/' || pathname == '/practicafinal' || pathname == '/practicafinal/'){
      this.isNotHome = false;
    }else{
      this.isNotHome = true;
    }
  },
  updated (){
    var pathname = window.location.pathname;
    if(pathname == '/' || pathname == '/practicafinal' || pathname == '/practicafinal/'){
      this.isNotHome = false;
    }else{
      this.isNotHome = true;
    }
  }
}
</script>

