import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Animals from '../views/Animals.vue'
import Veins from '../views/Veins.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/animals',
    name: 'Animals',
    component: Animals
  },
  {
    path: '/veins',
    name: 'Veins',
    component: Veins
  }
]

const router = new VueRouter({
  mode: "history",
  base: '/practicafinal/',
  routes
})

export default router
