<template>
    <main class="animals">
        <div class="container">
            <h1>Animals</h1>
            <b-tabs justified tabindex="0">
                <b-tab title="Fish" active>
                    <TableAnimals :taula='fishes' :files='fishesFields' color='primary' id='fishesTable' :perPage='perPage' :currentPage='currentPage' :rows='rowsFishes'/>
                </b-tab>
                <b-tab title="Sea Creatures">
                    <TableAnimals :taula='seaC' :files='seaCFields' color='danger' id='seaCTable' :perPage='perPage' :currentPage='currentPage' :rows='rowsSeaC'/>
                </b-tab>
                <b-tab title="Bugs">
                    <TableAnimals :taula='bugs' :files='bugsFields' color='success' id='bugsTable' :perPage='perPage' :currentPage='currentPage' :rows='rowsBugs'/>
                </b-tab>
            </b-tabs>
        </div>
    </main>
</template>

<script>
import TableAnimals from '@/components/TableAnimals.vue';
import axios from "axios";

export default {
    name: 'Animals',
    components: {
        TableAnimals
    },
    mounted () {
        axios
            .get('https://acnhapi.com/v1/fish')
            .then((response) => {
                for(var i in response.data)
                this.fishes.push(response.data[i]);
            }),
        axios
            .get('https://acnhapi.com/v1/sea')
            .then((response) => {
                for(var i in response.data)
                this.seaC.push(response.data[i]);
            })
        axios
            .get('https://acnhapi.com/v1/bugs')
            .then((response) => {
                for(var i in response.data)
                this.bugs.push(response.data[i]);
            })
    },
    data: function(){
        return{
            fishesFields: [
                { key: 'name.name-USen', label: 'Name' },
                { key: 'availability.location', label: 'Location' },
                { key: 'availability.rarity', label: 'Rarity' },
                { key: 'price', label: 'Price' }
            ],
            fishes: [],
            seaC:[],
            seaCFields: [
                { key: 'name.name-USen', label: 'Name' },
                { key: 'speed', label: 'Speed' },
                { key: 'shadow', label: 'Shadow' },
                { key: 'price', label: 'Price' }
            ],
            bugs:[],
            bugsFields: [
                { key: 'name.name-USen', label: 'Name' },
                { key: 'availability.location', label: 'Location' },
                { key: 'availability.rarity', label: 'Rarity' },
                { key: 'price', label: 'Price' }
            ],
            perPage: 10,
            currentPage: 1,
        }
    },
    computed: {
      rowsFishes() {
        return this.fishes.length
      },
      rowsSeaC() {
        return this.seaC.length
      },
      rowsBugs() {
        return this.bugs.length
      }
    }
}
</script>

<style>
    td{
        text-transform: capitalize;
    }
    .tab-content{
        background-color: #f8f8f0;
    }
    .tab-pane{
        padding: 1.5rem;
    }
    
</style>